export default {
	data() {
		return {
			commdity: [], //商品
			slideshow: [], //轮播
			show: false,
			popupimg:'',
			step:1,
			comnum:'',
			but:''
		}
	},
	onLoad(news) {
		// console.log(news)
		var item = JSON.parse(decodeURIComponent(news.news));
		console.log(item)
		this.commdity = item;
		this.slideshow = item.slideshow
		console.log(this.slideshow)
		this.popupimg = item.slideshow[0].slideshow
		// this.comnum = uni.getStorageSync('comnum')
	},
	methods: {
